import { loadCachedVariableAsBooleanOrNull, saveCachedVariable } from "./library";
let pos = 0;
let active = false;
export default function applyHooks() {
    let toc = document.getElementById("toc_container");
    if (toc == null)
        return;
    pos = toc.getBoundingClientRect().top - 14.25;
    //document.addEventListener("scroll", debounce(function(){updateNavPosition(toc)}, 50, {maxWait:500}));
    document.addEventListener("scroll", function () { updateNavPosition(toc); });
    let check = document.getElementById("toc-follow-toggle");
    let saved = loadCachedVariableAsBooleanOrNull("quicknav-scroll");
    if (saved != null)
        check.checked = saved;
    active = check.checked;
    check.addEventListener("click", updateTOCFollowStatus);
}
function updateTOCFollowStatus() {
    let toc = document.getElementById("toc_container");
    let check = document.getElementById("toc-follow-toggle");
    active = check.checked;
    if (check.checked) {
        updateNavPosition(toc);
    }
    else {
        toc.style.top = pos + "px";
    }
    saveCachedVariable("quicknav-scroll", active.toString());
}
function updateNavPosition(toc) {
    if (!active)
        return;
    // let toc = document.getElementById("toc_container") as HTMLElement;
    //let text = (document.getElementById("textarea") as HTMLElement).getBoundingClientRect().top;
    //let nav = (document.getElementById("navbar") as HTMLElement).getBoundingClientRect();
    // let pad = text-nav.bottom;
    //console.log("Screen scroll is "+window.scrollY+"; nav bounds are "+nav.top+" > "+nav.bottom+"; text begins at "+text.getBoundingClientRect().top+" -> pad ="+pad);
    // window.requestAnimationFrame(function(){toc.style.top = Math.max(text, window.scrollY+4)+"px"});
    let val = Math.max(pos, window.scrollY + 6);
    /*
    if (window.scrollY >= pos) {
         val = Math.min(val);
    }*/
    toc.style.top = val + "px";
}
